import { NextRequest, NextResponse } from 'next/server';
import { getServerSession } from 'next-auth/next';
import { auth } from '@/lib/auth'; // Our NextAuth configuration
import db from '@/lib/db'; // Your existing database connection

export async function GET(request: NextRequest) {
  try {
    // Get the NextAuth session
    const session = await auth();
    if (!session || !session.user) {
      return NextResponse.json({
        error: 'Unauthorized'
      }, {
        status: 401
      });
    }

    // Get user info from session
    const {
      user
    } = session;

    // Check if user exists in our database, create if not
    let dbUser;
    try {
      // Check if user exists in database by email or Google ID
      const existingUserQuery = await db.query('SELECT id, email, name, role, avatar FROM users WHERE email = $1 OR google_id = $2', [user.email || '', user.id || '']);
      if (existingUserQuery.rows.length > 0) {
        // User already exists
        dbUser = existingUserQuery.rows[0];
      } else {
        // Create new user
        const insertResult = await db.query(`INSERT INTO users (name, email, google_id, avatar, role)
           VALUES ($1, $2, $3, $4, $5)
           RETURNING id, email, name, role, avatar`, [user.name || user.email?.split('@')[0] || 'Google User', user.email || '', user.id || '', user.image || null, 'user']);
        dbUser = insertResult.rows[0];
      }
    } catch (dbError) {
      console.error('Database error during Google auth:', dbError);
      // If database operation fails, still return session user info
      return NextResponse.json({
        user: {
          id: user.id,
          email: user.email,
          name: user.name || user.email?.split('@')[0] || 'Google User',
          role: 'user',
          avatar: user.image || null,
          googleId: user.id
        }
      });
    }

    // Return user information
    return NextResponse.json({
      user: {
        id: dbUser.id,
        email: dbUser.email,
        name: dbUser.name,
        role: dbUser.role,
        avatar: dbUser.avatar,
        googleId: user.id
      }
    });
  } catch (error) {
    console.error('Error in Google auth API:', error);
    return NextResponse.json({
      error: 'Internal server error'
    }, {
      status: 500
    });
  }
}